import test from 'ava';
import mongoose from 'mongoose';
import User from '../../src/user';
import {
  mongooseConnect,
  mongooseDisconnect,
} from '../test_helper';

test.before('connect to database', async (t) => {
  await mongooseConnect('users_test');
});

test.after.always('disconnect from database', async () => {
  await mongooseDisconnect();
});

test.beforeEach('drop users table', async (t) => {
  try {
    await mongoose.connection.collections.users.drop();
  }
  catch (err) {
    // IGNORE EXCEPTION
  }
});

test.serial('Creating subdocuments', async (t) => {
  const joe = new User({
    name: 'Joe',
    posts: [{ title: 'post_title' }],
  });
  await joe.save();
  const queriedUser = await User.findOne({ name: 'Joe' });
  t.is( queriedUser.posts[0].title, 'post_title', 'queriedUser has a post with appropriate postTitle' );
});

test.serial('Adding subdocuments to existing record', async (t) => {
  const joe = new User({
    name: 'Joe',
    posts: [],
  });
  await joe.save();
  const queriedUser = await User.findOne({ name: 'Joe' });
  t.pass();
});
